import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { formatGram, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class LaporanExelBatalJual extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let stock_on_hand = 0;
    let berat = 0;
    let berat_atribut = 0;
    let total_barang = 0;
    let harga_jual = 0;
    let harga_atribut = 0;
    let harga_gram = 0;
    let harga_total = 0;
    let ongkos = 0;
    this.props.data.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        stock_on_hand = stock_on_hand + item.stock_on_hand;
        berat_atribut = berat_atribut + item.berat_atribut;
        berat = berat + item.berat;
        harga_jual = harga_jual + item.harga_jual;
        ongkos = ongkos + item.ongkos;
        harga_total = harga_total + item.harga_total;
        harga_gram = harga_gram + item.harga_gram;
        harga_atribut = harga_atribut + item.harga_atribut;
      });
      total_barang = total_barang + barang.detail.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Batal Penjualan"
          sheet="Laporan Batal Penjualan"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="11" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN BATAL PENJUALAN{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="11"> Periode </th>
            </tr>
            <tr>
              <th colSpan="11">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="11"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="11"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr></tr>
            <tr>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    KODE DEPT
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    KODE BARCODE
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    NAMA BARANG
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    NAMA ATRIBUT
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                    BERAT
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                    ONGKOS
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                    HARGA JUAL
                  </td>
                
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                    BERAT ATRIBUT
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                    HARGA ATRIBUT
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                    HARGA GRAM
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                    HARGA TOTAL
                  </td>
                </tr>
          </thead>
          {this.props.data.map((item, index) => (
            <>
              <tbody>
                {item._id.pembayaran.map((header_detail, index) => (
                  <>
                    {/* <tr>
                      <td style={{ textAlign: "center" }}>No Faktur Group</td>
                      <td colSpan="10" style={{border: "1px solid #000"}}> {header_detail.no_faktur_group}</td>
                    </tr> */}
                    <tr>
                      <td colSpan="11">
                        {header_detail.jenis}{" "}
                        {parseInt(header_detail.jumlah_rp).toLocaleString(
                          "kr-KO"
                        )}
                      </td>
                    </tr>
                  </>
                ))}
               
                {item.detail.map((row, index) => (
                  <tr>
                    <td>
                      {row.kode_dept}
                    </td>
                    <td>
                      &nbsp;{row.kode_barcode}
                    </td>
                    <td>
                      {row.nama_barang}
                    </td>
                    <td>
                      {row.nama_atribut}
                    </td>
                    <td style={{textAlign: "right"}}>
                      {formatGram(row.berat,3)}
                    </td>
                    <td style={{textAlign: "right"}}>
                      {row.ongkos.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign: "right"}}>
                      {row.harga_jual.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign: "right"}}>
                      {formatGram(row.berat_atribut,2)}
                    </td>
                    <td style={{textAlign: "right"}}>
                      {row.harga_atribut.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign: "right"}}>
                      {row.harga_gram.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign: "right"}}>
                      &nbsp;{row.harga_total.toLocaleString("kr-KO")}
                    </td>
                  </tr>
                ))}
              </tbody>
              <tfoot>
               
              </tfoot>
            </>
          ))}
          <tr>
            <td colSpan="4" style={{ backgroundColor: "#E8E5E5", color: "#000",  textAlign: "center" }}>
             Grand Total Barang : {total_barang}
            </td>

            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right"  }}>
              {formatGram(berat,3)}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right"   }}>
              {ongkos.toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",  textAlign: "right" }}>
              {harga_jual.toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right"  }}>
              {formatGram(berat_atribut,2)}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",  textAlign: "right" }}>
              {harga_atribut.toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",  textAlign: "right" }}>
              {harga_gram.toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",  textAlign: "right" }}>
              {harga_total.toLocaleString("kr-KO")}
            </td>
          </tr>
        </table>
      </>
    );
  }
}
export default LaporanExelBatalJual;
